nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    z-index: 2;
    position: fixed;
    left: 7%;
    transform: translateX(-50%);
    bottom: 23rem;
    display: inline;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
    backdrop-filter: blur(15px);
}

nav a{

    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
   
}

nav a:hover{

    background: rgba(0, 0, 0, 0.4);
}

nav a.active{

    background: var(--color-primary);
    color: var(--color-bg);
}